import React, { Component } from 'react';
import { BrowserRouter, Route, Switch } from 'react-router-dom';
import Header from './Header';
import About from './About';
import Contact from './Contact';
import Error from './Error';
import DiningSet from './catalog/DiningSet';
import BarSet from './catalog/BarSet';
import DeepSeating from './catalog/DeepSeating';
import SingleProduct from "./SingleProduct";
import Footer from "./Footer";
import 'font-awesome/css/font-awesome.min.css';
import PlaceOrder from "./order/PlaceOrder";
import ExtendableSet from "./catalog/ExtendableSet";

class App extends Component {
    render() {
        return (
            <BrowserRouter>
                <div>
                    <Header />
                    <br/>
                    <Switch>
                        <Route path="/" component={ExtendableSet} exact/>
                        <Route path="/about" component={About}/>
                        <Route path="/contact" component={Contact}/>
                        <Route path="/dining-sets" component={DiningSet}/>
                        <Route path="/bar-sets" component={BarSet}/>
                        <Route path="/deep-seating" component={DeepSeating}/>
                        <Route path="/extendable-table-sets" component={ExtendableSet} />
                        <Route path="/single-product/:name" component={SingleProduct}/>
                        <Route path="/order/:sku" component={PlaceOrder}/>
                        <Route component={Error}/>
                    </Switch>
                    <Footer/>
                </div>
            </BrowserRouter>
        );
    }
}
export default App;